.App {
  text-align: center;
  background-size: 100% 200%;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #F44336), color-stop(50%, #4CAF50));
  background-image: -webkit-linear-gradient(top, #F44336 50%, #4CAF50 50%);
  background-image: -o-linear-gradient(top, #F44336 50%, #4CAF50 50%);
  background-image: linear-gradient(to bottom, #F44336 50%, #4CAF50 50%);
  color: #EEE;
  -webkit-transition: background-position 1s;
  -o-transition: background-position 1s;
  transition: background-position 1s;
}

.App-working {
}

.App-break {
  background-position: 0 -100%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



